import React from 'react';
import {Redirect, Route, Switch, withRouter} from "react-router-dom";
import {Progress} from "element-react";
import Label from "arui-feather/label";
// import ProductsForm from './Forms/ProductsForm'
import Heading from "arui-feather/heading";
import {addCompanyTabs} from "../../_store/accountForm";
import {connect} from "react-redux";


function AddCompany({clientType, ...props}) {
  
  const tabs = [...addCompanyTabs]
    .filter(tab => tab.acceptFor.indexOf(clientType) !== -1);
  
  const activeRoute = props.history.location.pathname;
  const activeIndex = tabs.findIndex(tab => tab.url === activeRoute);
  const activeTab = tabs.find(tab => tab.url === activeRoute);
 
  function handleSubmit() {
    if (tabs[activeIndex+1]) {
      props.history.push(tabs[activeIndex+1].url);
    }
  }
  
  return (
    <div className="add-company-page">
      <div className="progress-bar mt-2  mb-4">
        <Label size="s" className="d-inline-block opacity-high ml-2 mb-1">Анкета заполнена на</Label>
        <Progress strokeWidth={18} percentage={Math.floor(12.5 * activeIndex)} status="exception" textInside />
      </div>
      { activeTab && <Heading size="l" className="mt-5 mb-4">{activeTab.header ? activeTab.header : activeTab.label}</Heading>}
      <div className="tab-content">
        <Switch>
          { tabs.map(({component: Component, ...tab}, index) => (
            <Route path={tab.url}
                   component={Component ? () => (<Component onSubmit={handleSubmit}/>) : () => <div>No content</div>}
                   key={'route_' + index}/>
          )) }
          <Redirect from="/dashboard/add/company" to={tabs[0].url}/>
        </Switch>
      </div>
    </div>
  )
}

const mapStateToProps = (state) => ({
  clientType: state.accountForm.clientType
});

export default connect(mapStateToProps)(withRouter(AddCompany));
